import React, { useEffect, useState } from 'react'
// import { Link } from 'react-router-dom'

const Home = () => {
  const [showBlur, setShowBlur] = useState(false)
  const [fristOpen, setFristOpen] = useState(true)
  useEffect(() => {
  }, [])
  const setShowBlurHandle = () => {
    setShowBlur(cur => !cur)
    setFristOpen(false)
  }
  return (
    <div className="home">
      <div className={`home-bg ${showBlur?'home-bg-blur':''}`}>
        <div className={`home-bg-title animate__animated ${!fristOpen?showBlur?'animate__bounceOutDown':'animate__bounceInUp':''}`}>
          <p className="animate__animated animate__bounceInLeft">测试标题</p>
        </div>
      </div>
      <div className="home-main">
        <div className="go-next-btn" onClick={setShowBlurHandle}>
          <div className="gn-top"></div>
          <div className="gn-bottom"></div>
        </div>
      </div>
    </div>
  )
}

export default Home
